<script>
// @ is an alias to /src

import slider from "vue3-slider"

import "vue3-circle-progress/dist/circle-progress.css";
import CircleProgress from "vue3-circle-progress";
import axios from "axios";

const SHA256 = require("crypto-js/sha256");
const SALT = ["B3C10688", "4491", "4BE6", "B663", "257F1F056E28"].join('-');

import {Client} from "colyseus.js";

const CONSTANTS = {
  //API_URL: "http://localhost:3000"
  API_URL: "https://apitg.hoh.game"
};

export default {
  name: 'App',
  components: {
    CircleProgress,
    "vue3-slider": slider
  },
  mounted() {
    this.initQueryParams();
    if (!this.chatId) {
      alert(this.trans('error_auth'));
      return;
    }
    this.init();
  },
  methods: {
    checkPlayerIsWinner(name) {
      return (this.winnersDic[name]);
    },
    getHandPowerIndex() {
      const dic = {
        'high card': 1,
        'one pair': 2,
        'two pairs': 3,
        'three of a kind': 4,
        'straight': 5,
        'flush': 6,
        'full house': 7,
        'four of a kind': 8,
        'straight flush': 9,
        'royal flush': 10
      };
      return dic[this.handPower];
    },
    getPointsByPlace(place) {
      switch (place) {
        case 1:
          return 1500;
        case 2:
          return 1000;
        case 3:
          return 500;
        default:
          return 0;
      }
    },
    goFromModalWinPositionToMenu() {
      //Скрываем модальное окно
      this.hideModalWinPosition();
      //Переходим в меню
      this.changeState(this.STATE.MENU);
    },
    showModalWinPosition() {
      this.isShowModalWinPosition = true;
    },
    hideModalWinPosition() {
      this.isShowModalWinPosition = false;
    },
    applyConfirmExitGame() {
      //1. Завершаем событие
      this.leaveRoom();
      //2. Закрываем модальное окно
      this.hideModalConfirmExitGame();
      //3. Изменение state
      this.changeState(this.STATE.MENU);
    },
    hideModalConfirmExitGame() {
      this.isShowModalConfirmExit = false;
    },
    showModalConfirmExitGame() {
      this.isShowModalConfirmExit = true;
    },
    raiseHotKey(value) {
      this.room.send("action", {
        action: "raise",
        value: value
      });
    },
    clickKeyboardNumber(number) {
      this.inputValue = String(this.inputValue) + number;
    },
    checkBetIsValid() {
      if (!String(this.inputValue).length) return false;
      const value = parseFloat(this.inputValue);
      return (value >= this.minInputValue && value <= this.maxInputValue);
    },
    clickKeyboard(code) {
      let valueStr;

      const specialCodes = ['erase', '.'];

      if (!specialCodes.includes(code)) {
        //На кол-во знаков после точки
        const prevValue = String(this.inputValue);
        if (prevValue.includes('.')) {
          //На максимальную длину
          if (prevValue.length === 6) return;
          const arr = prevValue.split('.');
          if (arr[1].length) {
            return;
          }
        } else {
          //Если точки не содержит На максимальную длину
          if (prevValue.length === 4) return;
        }
      }

      switch (code) {
        case "erase":
          valueStr = String(this.inputValue);
          this.inputValue = valueStr.substring(0, valueStr.length - 1);
          break;
        case ".":
          valueStr = String(this.inputValue);
          //На наличие точки
          if (valueStr.includes('.')) return;
          this.inputValue += ".";
          break;
        case "0":
          this.clickKeyboardNumber(0);
          break;
        case "1":
          this.clickKeyboardNumber(1);
          break;
        case "2":
          this.clickKeyboardNumber(2);
          break;
        case "3":
          this.clickKeyboardNumber(3);
          break;
        case "4":
          this.clickKeyboardNumber(4);
          break;
        case "5":
          this.clickKeyboardNumber(5);
          break;
        case "6":
          this.clickKeyboardNumber(6);
          break;
        case "7":
          this.clickKeyboardNumber(7);
          break;
        case "8":
          this.clickKeyboardNumber(8);
          break;
        case "9":
          this.clickKeyboardNumber(9);
          break;
      }
    },
    onClickKeyboardBet() {
      const prevValue = parseFloat(this.inputValue || 0);

      if (prevValue > this.maxInputValue) {
        this.inputValue = this.maxInputValue;
        this.inputValueSlider = this.maxInputValue;
      }

      if (prevValue < this.minInputValue) {
        this.inputValue = this.minInputValue;
        this.inputValueSlider = this.maxInputValue;
      }
    },
    togglePowerHands() {
      this.isShowPowerHands = !this.isShowPowerHands;
    },
    onChangeSlider(value) {
      this.inputValue = this.inputValueSlider;
    },
    toggleSoundOn() {
      this.soundOn = !this.soundOn;
      localStorage.setItem('soundOn', this.soundOn);
      if (!this.soundOn) {
        //TODO звуки .pause
      } else {
        //TODO звуки .play
      }
    },
    toggleMusicOn() {
      this.musicOn = !this.musicOn;
      localStorage.setItem('musicOn', this.musicOn);
      if (!this.musicOn) {
        //Отключаем музыку
        this.audioGame.pause();
        this.audioMenuChill.pause();
      } else {
        if (this.getState() === this.STATE.MENU) {
          this.audioMenuChill.play();
        }
        if (this.getState() === this.STATE.GAME) {
          this.audioGame.play();
        }
      }
    },
    getValueActionCall(value) {
      if (!value) return;
      const player = this.getMyPlayer();
      if (!player?.balance) return;
      if (value > player.balance) {
        return player.balance;
      }
      return value;
    },
    actionCall() {
      this.room.send("action", {
        action: "call"
      });
    },
    actionCheck() {
      this.room.send("action", {
        action: "check"
      });
    },
    actionFold() {
      this.room.send("action", {
        action: "fold"
      });
    },
    actionRaise() {
      this.room.send("action", {
        action: "raise",
        value: this.raise
      });
    },
    actionBet() {
      //Дополнительно валидируем значение в inputValue
      const value = parseFloat(this.inputValue || 0);
      if (value > this.maxInputValue) {
        this.inputValue = this.maxInputValue;
      }
      if (value < this.minInputValue) {
        this.inputValue = this.minInputValue;
      }

      if (this.isMyPlayerActorHasBet()) {
        this.room.send("action", {
          action: "bet",
          value: this.inputValue
        });
        return;
      }
      if (this.isMyPlayerActorHasRaise()) {
        this.room.send("action", {
          action: "raise",
          value: this.inputValue
        });
        return;
      }
    },

    cancelFindGame() {
      //Отписываемся от всех событий web-socket client room
      this.removeRoomsEvents();

      //Скрываем модальное окно поиска игры
      this.hideModalFindGame();

      //Переходим в меню
      this.goToMenu();

    },
    formatBalance(number) {
      const str = String(number);
      if (str.indexOf('.') !== -1) {
        const arr = str.split('.');
        return [this.formatNumber(arr[0]), arr[1][0]].join('.');
      }
      return number;
    },
    formatNumber(number) {
      return new Intl.NumberFormat().format(number).replace(/,/gi, ' ');
    },
