    getCardCode(card) {
      if (card) {
        return card.rank + card.suit;
      }
      return "";
    },
    isMyPlayer(player) {
      return (1 === this.getClassPosition(player));
    },
    isPlayerActor(player) {
      return ((player.order - 1) === this.roomState?.table?.actorPosition)
    },
    isMyPlayerActorHasBet() {
      return this.roomState?.table?.actorLegalActions?.includes('bet');
    },
    isMyPlayerActorHasRaise() {
      return this.roomState?.table?.actorLegalActions?.includes('raise');
    },
    getMyPlayer() {
      for (let playerId in this.roomState?.players) {
        const player = this.roomState.players[playerId];
        if (this.isMyPlayer(player)) {
          return player;
        }
      }
    },
    isMyPlayerActor() {
      for (let playerId in this.roomState?.players) {
        const player = this.roomState.players[playerId];
        if (
            this.isMyPlayer(player) &&
            this.isPlayerActor(player)
        ) {
          return true;
        }
      }
      return false;
    },
    getClassPosition(player) {
      const myPlayer = this.roomState.players[this.room.sessionId];
      if (!myPlayer) return;
      const myPosition = myPlayer.order;
      const offsetPosition = 1 - myPosition;
      let position = player.order + offsetPosition;
      if (position < 1) {
        position = 6 + position;
      }
      return position;
    },
    async showModalCountDown() {
      //Скрываем модальное окно обратного отчета
      this.hideModalFindGame();

      //Начинаем отсчитывать таймер
      this.textCountDownStart = "3"
      await this.sleepSec(1);
      this.textCountDownStart = "2"
      await this.sleepSec(1);
      this.textCountDownStart = "1"
      await this.sleepSec(1);
      this.textCountDownStart = "GO!"
      await this.sleepSec(1);
      this.isProcessSitDown = false;
    },
    initRoomEvents() {
      /*
      this.room.state.players.onAdd = (player, sessionId) => {
        player.onChange = (changes) => {
        }
      }
      this.room.state.players.onRemove = (player, sessionId) => {
        //delete this.players[sessionId];
      }
      */

      //Room state has been updated:
      this.room.onStateChange((state) => {

        if (!this.isGameStarted) {
          if (state.table.status === "STARTED") {
            //Скрываем модальное окно
            //this.hideModalFindGame();
            //Меняем статус у стола
            this.isGameStarted = true;
          }
        }
        //TABLE

        //Удаляем старых
        for (let prop in this.roomState?.players) {
          delete this.roomState?.players[prop];
        }

        //Инициализируем заново
        for (let [key, player] of state.players) {
          if (!key) continue;
          this.roomState.players[key] = {
            balance: player.balance,
            avatarId: player.avatarId,
            order: player.order,
            name: player.name,
            lastAction: player.lastAction,
            lastActionValue: player.lastActionValue,
            bet: player.bet,
            raise: player.raise,
            hand: player.hand,
            folded: player.folded,
            left: player.left,
            showCards: player.showCards,
            cards: player.cards,
            connected: player.connected,
            timeActionSec: player.timeActionSec,
            winPosition: player.winPosition
          };
        }

        //Init Table
        const tableFields = [
          "actorLegalActions",
          "actorPosition",
          "ante",
          "bettingRound",
          "bigBlind",
          "bigBlindPosition",
          "dealerPosition",
          "level",
          "pot",
          "rounds",
          "smallBlind",
          "smallBlindPosition",
          "status",
          "currentPotAmount",
          "sumPotsAmount",
          "communityCards",
          "winners",//[id_1, id_2]
          //Raise
          "currentBet",
          "lastRaise",
          "minRaise",
          "maxRaise"
        ];
        for (let field of tableFields) {
          if (state.table[field] !== undefined) {
            this.roomState.table[field] = state.table[field];
          }
        }

        //Обновляем переменные для inputValue
        //Сделать нам потребуется метод, что ход перешел от одного игрока к другому,
        //Для того, чтобы понять minRaise, maxRaise - для Raise, и для bet

        //1. Если это наш игрок
        if (this.lastActorPosition !== this.roomState?.table?.actorPosition) {
          this.lastActorPosition = this.roomState?.table?.actorPosition;
          this.onChangeActor();
        }

        //Присваиваем победителей
        if (this.isRoundEnd) {
          this.winnersDic = {};
          if (this.roomState.table?.winners.length) {
            for (let winner of this.roomState.table?.winners) {
              this.winnersDic[winner.name] = 1;
            }
          }
        }

        //Если игроку пришло winPosition
        const player = this.getMyPlayer();
        if (player.winPosition) {
          //1. Сохраняем занятую позицию
          this.winPosition = player.winPosition;
          //2. Отписываемся от событий комнаты
          this.leaveRoom();
          //3. Показываем модальное окно
          setTimeout(() => {
            this.showModalWinPosition();
          }, 6000);
        }

      });

      //Подписываем на сообщения комнаты но только private
      this.room.onMessage("*", (type, data) => {

        //TODO сделать событие игра началась
        if (type === "gameStart") {
          console.warn("Game started");
          this.isRoundEnd = false;
          this.cards = [];
          this.winnersDic = {};
          return;
        }

        if (type === "allSitDown") {
          console.warn("The players are seated at the table");
          //Показать модальное окно с 3-2-1-go и закрыть его
          this.showModalCountDown();

          return;
        }

        //Закончена игра
        if (type === "gameEnd") {
          console.warn("Game ended");
          this.isRoundEnd = true;
          this.cards = [];
          this.winnersDic = {};
          return;
        }

        //Закончен раунд
        if (type === "roundEnd") {
          console.warn("Round end");
          this.isRoundEnd = true;
          return;
        }

        //Раздача карт
        if (type === "dealCards") {
          console.warn("Deal cards");
          if (this.soundOn) {
            //this.audioDealCards.play();
          }
          this.cards = data;
          this.isRoundEnd = false;
          this.winnersDic = {};
          return;
        }

        //Раздача карт
        if (type === "updateClientHandPower") {
          console.warn("Update power of hand");
          this.handPower = data.handName;
          this.handPowerIndex = this.getHandPowerIndex();
          return;
        }

      });

      //Server error occurred:
      /*
      this.room.onError((code, message) => {
        //console.log(this.client.id, "couldn't join", this.room.name);
      });
      */

      //The client left the room:
      this.room.onLeave((code) => {
        //console.log(this.client.id, "left", this.room.name);
        this.players = {};
        this.roomState = {};
        //this.room.removeAllListeners();
      });

    },

    initRoomChat(roomId) {
      this.client.joinOrCreate("chat", {parentRoomId: roomId}).then(room => {
        this.roomChat = room;
        /*
        room.onStateChange.once((state) => {
          console.log("initial room state:", state);
        });
        */

        // new room state
        /*
        this.roomChat.onStateChange((state) => {
          // this signal is triggered on each patch
        });
        */

        // listen to patches coming from the server
        this.roomChat.onMessage("messages", (message) => {
          this.messages.push(message);
        });
      });
    },
    //Показываем окно с таймером 2:00
    showModalWithTimer() {
      //Показываем окно с пустым столом
      this.showModalFindGame();
      //Запускаем таймер
      this.runTickTimerFindGame(1);
      //Запускаем таймер на 2 минуты
      this.runProgressFindGame();
    },
    runTickTimerFindGame(min) {
      return new Promise((resolver) => {
        if (min < 10) {
          this.timerFindGameClock = "0" + min + ":00";
        } else {
          this.timerFindGameClock = min + ":00";
        }

        let timerSec = min * 60;
        this.intervalTimerFindGame = setInterval(() => {
          timerSec--;
          const time = timerSec / 60;
          const minutes = Math.trunc(time);
          let minutesFormat;
          if (minutes < 10) {
            minutesFormat = "0" + minutes;
          } else {
            minutesFormat = minutes;
          }
          const seconds = Math.round((time - minutes) * 60);
          let secondsFormat;
          if (seconds < 10) {
            secondsFormat = "0" + seconds;
          } else {
            secondsFormat = seconds;
          }
          this.timerFindGameClock = [minutesFormat, secondsFormat].join(':');

          if (timerSec === 0) {
            clearInterval(this.intervalTimerFindGame);
            resolver(true);
          }

        }, 1000);
      });
    },
    trans(code) {
      const lang = this.user.lang || 'ru';
      if (!this.lang[lang]) return '';
      if (!this.lang[lang][code]) return '';
      return this.lang[lang][code];
    },
    onInput() {
      this.runProcessCheckNickname();
    },
    onKeyUp(event) {
      //if (['Delete', 'Backspace'].includes(event.key)) {
      //this.runProcessCheckNickname();
      //}
    },
    onKeypressNickname(event) {
      const allowed = /[A-Za-z0-9]/g;
      const key = String.fromCharCode(event.keyCode);
      if (!allowed.test(key)) {
        event.preventDefault();
        return false;
      }
    },
    onKeypressBet(event) {
      const allowed = /[0-9.]/g;
      const key = String.fromCharCode(event.keyCode);

      //Защита на разрешенные символы
      if (!allowed.test(key)) {
        event.preventDefault();
        return false;
      }

      const prevValue = String(this.inputValue);
      const isPrevValueHasDot = String(this.inputValue).includes('.');
      //Защита от двойной точки
      if (key === '.') {
        if (isPrevValueHasDot) {
          event.preventDefault();
          return false;
        }
      }
      //Проверить, чтобы нельзя было больше одного символа после точки
      if (isPrevValueHasDot) {
        const arr = prevValue.split('.');
        if (arr[1].length === 1) {
          event.preventDefault();
          return false;
        }
      }

    },
    checkBet() {
      if (parseFloat(this.inputValue) > this.maxInputValue) {
        this.inputValue = this.maxInputValue;
        this.inputValueSlider = this.maxInputValue;
      }
      if (parseFloat(this.inputValue) < this.minInputValue) {
        this.inputValue = this.minInputValue;
        this.inputValueSlider = this.minInputValue;
      }
    },
    runProcessCheckBet() {
      if (!this.debounceFunc) {
        this.debounceFunc = this.debounce(this.checkBet.bind(this), 1000);
      }
      this.debounceFunc();
    },
    runProcessCheckNickname() {
      if (this.nickname.length < 5) {
        this.isValidNickname = undefined;
        return;
      }
      if (!this.debounceFunc) {
        this.debounceFunc = this.debounce(this.checkNickname.bind(this), this.checkNicknameDebounceMsec);
      }
      this.debounceFunc();
    },
    checkNicknameOnServer() {
