    startGame() {
      //Если нет пользователя, то это ошибка
      if (!this.user) return;

      //Check isBlocked
      if (this.user.is_blocked) {
        alert(this.trans('error_is_blocked'));
        return;
      }

      //Audio
      this.initAudio();

      //Init avatar
      this.initAvatar();

      //Init selected table
      this.selectedTableId = parseInt(this.tableId || 1);

      //Проверяем, есть ли у пользователя
      if (!this.user.login) {
        //Переходим к экрану с вводом nickname
        this.goToNickname();
      } else {
        //Переходим к экрану с игрой
        this.goToMenu();
      }
