    getTableImageUrl() {
      for (let table of this.tables) {
        if (table.id === this.tableId) {
          return table.url;
        }
      }
    },
    async saveSelectedUserTable() {
      this.tableId = parseInt(this.selectedTableId);
      localStorage.setItem('tableId', this.tableId);
      this.hideSelectTable();
    },
    saveUserTable(tableId) {
      //1. Сохраняем стол у пользователя в бд
      return new Promise(async (resolver) => {
        try {
          const str = [this.chatId + tableId + SALT].join('');
          const secure = SHA256(str).toString();

          const {data} = await axios.post(
              CONSTANTS.API_URL + "/user/update-table", {
                chat_id: this.chatId,
                table_id: tableId,
                secure: secure
              });
          resolver(data);
        } catch (e) {
          resolver(false);
        }
      });
    },
    async goToRating() {
      //Вообще здесь бы обновить информацию по пользователю
      this.showSpinner();
      await this.sleepSec(1);

      await Promise.allSettled([
        this.loadRating()
      ]);
      //Инициализируем avatar
      this.initRating();
      this.hideSpinner();
      this.changeState(this.STATE.RATING);
    },
    showSpinner() {
      this.isShowSpinner = true;
    },
    hideSpinner() {
      this.isShowSpinner = false;
    },
    async init() {
      //Инициализируем localStorage
      this.initLocalStorage();

      //Экран загрузки (получение данных пользователя)
      await this.goToLoading();

    },
    initAvatar() {
      for (let avatar of this.avatars) {
        if (avatar.id === this.user.avatar_id) {
          this.user.avatar = avatar;
        }
      }
    },
    initAudio() {
      //Музыка меню
      this.audioMenuChill = new Audio('./assets/music/chill_2.m4a');
      this.audioMenuChill.loop = true;
      this.audioMenuChill.volume = 0.1;

      //Музыка игры
      this.audioGame = new Audio('./assets/music/game.m4a');
      this.audioGame.loop = true;
      this.audioGame.volume = 0.1;

      //Раздача карт
      this.audioDealCards = new Audio('./assets/music/deal_cards.m4a');
      this.audioDealCards.loop = false;
      this.audioDealCards.volume = 0.1;
    },
    changeState(state) {
      switch (state) {
        case this.STATE.LOADING:
          break;
        case this.STATE.RATING:
          break;
        case this.STATE.MENU:
          this.audioGame.pause();
          if (this.musicOn) {
            this.audioMenuChill.play();
          }
          //Меняем статус у запуска игры
          this.isGameStarted = false;
          break;
        case this.STATE.GAME:
          this.audioMenuChill.pause();
          if (this.musicOn) {
            this.audioGame.play();
          }
          break;
        case this.STATE.NICKANME:
          break;
      }
      this.setState(state);
    },
    debounce(func, timeout) {
      let timer;
      return (...args) => {
        clearTimeout(timer);
        timer = setTimeout(() => {
          func.apply(this, args);
        }, timeout);
      }
    },

    showModalFindGame() {
      this.isShowModalFindGame = true;
    },
    hideModalFindGame() {
      //Сброс интервала таймера
      clearInterval(this.intervalTimerFindGame);
      //Сброс интервала прогресса анимации круга
      clearInterval(this.intervalProgressFindGame);
      //Скрываем модальное окно
      this.isShowModalFindGame = false;
    },

    toggleBetBtn() {
      this.isShowBetSettings = !this.isShowBetSettings;
    },

    //Выход из комнаты
    leaveRoom() {
      this.removeRoomsEvents();
    },
    //Отписываемся от событий комнаты
    removeRoomsEvents() {
      //Отписка от игровой комнаты
      if (this.room) {
        if (this.room.leave) {
          this.room.leave();
        }
        if (this.room.removeAllListeners) {
          this.room.removeAllListeners();
        }
      }

      //Отписка от чат комнаты
      if (this.roomChat) {
        if (this.roomChat.leave) {
          this.roomChat.leave();
        }
        if (this.roomChat.removeAllListeners) {
          this.roomChat.removeAllListeners();
        }
      }
    },
    onChangeActor() {
      this.isShowBetSettings = false;
      if (this.isMyPlayerActor()) {
        const player = this.getMyPlayer();

        //Если наш игрок имеет bet
        if (this.isMyPlayerActorHasBet()) {

          const currentBet = (this.roomState?.table?.currentBet || 1);

          if (currentBet > player.balance) {
            this.minInputValue = player.balance;
            this.maxInputValue = player.balance;
          } else {
            this.minInputValue = currentBet;
            this.maxInputValue = player.balance;
          }

          //ONLY ONE Обязательное сравнение, чтобы не сбить настройки
          if (this.inputValue !== this.minInputValue) {
            this.inputValue = this.minInputValue;
          }
        }

        //Если наш игрок имеет raise
        if (this.isMyPlayerActorHasRaise()) {
          const minRaise = this.roomState?.table?.minRaise;
          this.minInputValue = minRaise;
          if (this.roomState?.table?.maxRaise > player.balance) {
            this.maxInputValue = player.balance;
          } else {
            this.maxInputValue = this.roomState?.table?.maxRaise;
          }

          //ONLY ONE Обязательное сравнение, чтобы не сбить настройки
          if (this.inputValue !== this.minInputValue) {
            this.inputValue = this.minInputValue;
          }

          this.hotKeyRaiseX2 = true;
          this.hotKeyRaiseX2Value = minRaise;

          const oneMinRaise = (minRaise / 2);

          this.hotKeyRaiseX3 = (oneMinRaise * 3 <= player.balance);
          this.hotKeyRaiseX3Value = oneMinRaise * 3;

          this.hotKeyRaiseX4 = (oneMinRaise * 4 <= player.balance);
          this.hotKeyRaiseX4Value = oneMinRaise * 4;

        }
      }
    }
  },
